@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body{background-image:linear-gradient(to right, white, black, white);background-color:black;display:flex;align-items:center;justify-content:center;min-height:100vh}@font-face{src:url(/static/media/fbfont.77c4a1fb.OTF);font-family:fbfont}@font-face{src:url(/static/media/instafont.0e94b13a.ttf);font-family:instafont}#app{background-image:linear-gradient(to right, yellow, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, yellow);border:7px solid yellow;border-radius:7px;padding:0.625rem}.container{margin-top:0.3125rem}#header{color:white;font-family:fbfont;letter-spacing:0.0625rem;word-spacing:-0.3125rem;margin-left:0.125rem;margin-top:0.5625rem;line-height:3.375rem;text-align:center;text-transform:uppercase;border-radius:1px;height:3.125rem;letter-spacing:0.0625rem;word-spacing:-0.125rem}#balance{color:yellow;margin-left:5.75rem}#total{color:#6e89a3;letter-spacing:0.0625rem;margin-top:-0.375rem;text-align:center}.incomeExpences{color:white;background-image:linear-gradient(to right, white, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, black, white);border:1px solid gray;padding:0.75rem;display:flex;margin-top:1.25rem}.incomeExpences div{flex:1 1;text-align:center}.incomeExpences div .money{font-size:1.25rem;letter-spacing:0.0625rem;margin:0.3125rem 0}.incomeExpences div .plus{color:#2ecc71}.incomeExpences div .minus{color:#c0392b}.incomeExpences div #income,.incomeExpences div #expense{text-transform:uppercase}.incomeExpences div:first-of-type{border-right:1px solid #dedede}.underlined{color:yellow;border-bottom:1px solid #bbb;padding-bottom:0.25rem;margin-top:1.4rem;text-align:center}#transactionList{list-style-type:none;margin-bottom:2.1875rem}#transactionList li{background-color:#fff;box-shadow:var(--box-shadow);color:#333;display:flex;justify-content:space-between;position:relative;padding:0.625rem;margin-top:0.625rem;margin-left:-2.5rem;width:20rem}#transactionList .plus{border-right:5px solid #2ecc71}#transactionList .minus{border-right:5px solid #c0392b}form label{color:white;font-family:Lato;margin-top:0.625rem}form div:first-of-type{margin-top:-0.9375rem}input[type="text"],input[type="number"]{border:1px solid #dedede;border-radius:0px;display:block;font-size:1rem;padding:0.625re;width:100%}#btnX{width:100%;border-radius:2px;margin-top:0.25rem;font-family:monospace;font-size:1.25rem;text-transform:capitalize;letter-spacing:-0.0625rem;word-spacing:-0.125rem}#btnY{cursor:pointer;background-color:#e74c3c;border:transparent;color:white;font-size:0.8125rem;position:absolute;top:1.375rem;left:0.4375rem;transform:translate(-100%, -50%);border-radius:7px}#btnY:focus{outline:none}@media (max-width: 1326px){html{font-size:100%}}@media (max-width: 1200px){html{font-size:95%}}@media (max-width: 1100px){html{font-size:90%}}@media (max-width: 1000px){html{font-size:85%}}@media (max-width: 900px){html{font-size:80%}}@media (max-width: 770px){html{font-size:75%}}@media (max-width: 680px){html{font-size:70%}}@media (max-width: 600px){html{font-size:65%}}@media (max-width: 530px){html{font-size:60%}}@media (max-width: 350px){html{font-size:55%}}@media (max-width: 250px){html{font-size:50%}}@media (max-width: 230px){html{font-size:45%}}@media (max-width: 210px){html{font-size:40%}}@media (max-width: 190px){html{font-size:35%}}

